/*/////////////////////*/
/* Affect List Buttons*/
.ft_list-btn-group {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;
  /*   padding-left: 4px;*/
}

.ft_pop-over-header-btn {
  color: #6b778c;
  padding: 10px 6px;
}

.ft_pop-over-header-btn.left-most {
  padding-left: 10px;
}

/*////////////////////*/
/* Affect Lists*/
/* LIST WIDTHS*/
/*////////////*/
/* narrow*/
.ft_list-width_narrow {
  width: 150px !important;
}

/**/
.ft_list-width_narrow .js-list-name-input {
  font-size: 0.7em;
  height: 40px;
  line-height: 1.3em;
}

/* TO DO: Does this double up on the above?*/
.ft_list-width_narrow .js-card-name {
  font-size: 0.7em;
  line-height: 1.3em;
}

/* shrink add new card text*/
.ft_list-width_narrow .js-open-card-composer {
  font-size: 0.7em;
}

/* shrink labels*/
.ft_list-width_narrow .js-card-labels {
  overflow: hidden;
  font-size: 0.7em;
}

/* shrink badges and due dates*/
.ft_list-width_narrow .badge-text {
  overflow: hidden;
  font-size: 0.7em;
}

